import { FeatherModule } from 'angular-feather';
import { Home, Archive } from 'angular-feather/icons';

const icons = {
  Home,
  Archive
};


@NgModule({
  declarations: [],
  imports: [
    CommonModule,
	FeatherModule.pick(icons)
  ],
  exports: [
    FeatherModule
  ]
})
export class IconsModule { }
